import { Component, Prop, h, State, Element } from '@stencil/core';
import { utils } from '../../utils/utils';

@Component({
  tag: 'yeti-demo',
  shadow: false,
})
export class YetiDemo {

  @Element() el: HTMLElement;

  @Prop({ attribute: 'type'}) iconCode: string = 'check_circle';

  @Prop({ attribute: 'icon-css'}) iconCSS?: string = '';

  @Prop() iconId?: string = utils.generateUniqueId();

  @Prop() alt?: string = "";

  @Prop() focusable?: boolean = false;

  @State() iLoveJSX: boolean = false;



  render() {

    return (

        <div class="yeti-demo">

            <div class="yeti-demo-actual">
                <slot></slot>
            </div>

            <div class="yeti-demo-code">
                <div class="yeti-demo-code-title_bar">
                    <div class="yeti-demo-code-title_bar-title">HTML</div>
                    <ul class="yeti-demo-code-title_bar-actions">
                        <li>Hi</li>
                        <li>Hello</li>
                    </ul>
                </div>

                <div class="yeti-demo-code-actual"></div>
            </div>
        </div>

    );
  }



  componentDidRender() {
    console.log("Yup.");
  }

}